/* ProfileStyles.css */

/* Styles for the overall container */
.profileContainer {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Styles for the heading */
.profileContainer h1 {
  font-size: 24px;
  color: #333;
}

/* Styles for the horizontal rule */
.profileContainer hr {
  margin: 15px 0;
  border: 0;
  border-top: 1px solid #ddd;
}
